.App{
    display: flex;
    flex-flow: row nowrap;
    height: 100%;
    justify-content: center;
    align-items: flex-start;
}

.leftboxhome{
    width: 15%;
    height: 100%;
    box-sizing: border-box;
    border-width: 2px;
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;
    align-items: flex-end;
    padding-top: 20px;
    padding-bottom: 20px;
    min-width: 87px;
}

.rightboxhome{
    flex-grow: 1;
    width: 15%;
    height: 100%;
    box-sizing: border-box;
    border-width: 2px;
    min-width: 87px;
}

.middleboxhome{
    flex-shrink: 1;
    width: 90%;
    height: 100%;
    box-sizing: border-box;
    border-width: 2px;
    border-style: solid;
}

@media (max-width: 1150px) {
    .leftboxhome{
        align-items: center;
        width: 7%;
    }
}